import React, { useEffect, useState } from 'react';
import 'ol/ol.css'; // Import zkladnho CSS OpenLayers
import Map from 'ol/Map'; // Import tdy Map
import View from 'ol/View'; // Import tdy View
import TileLayer from 'ol/layer/Tile'; // Import tdy TileLayer
import OSM from 'ol/source/OSM'; // Import tdy OSM
import { fromLonLat, toLonLat } from 'ol/proj'; // Import funkce pro pevod souadnic
import { Draw } from 'ol/interaction'; // Import interakc pro kreslen
import { Vector as VectorLayer } from 'ol/layer'; // Import VectorLayer
import { Vector as VectorSource } from 'ol/source'; // Import VectorSource
import * as turf from '@turf/turf'; // Import knihovny Turf.js

function App() {
  const [map, setMap] = useState(null); // Stav pro mapu
  const [bollean, setBollean] = useState(null);
  const [lines, setLines] = useState([]); // Stav pro uchovvn seek
  const [miles, setMiles] = useState(false);
  const [rad, setRad] = useState(false);

  useEffect(() => {
    console.log(miles)
  }, [miles])

  // useEffect pro pidvn a odebrn interakc pi zmn bollean
  useEffect(() => {
    if (map) {
      const vectorSource = new VectorSource(); // Vektorov zdroj pro seky
      const draw = new Draw({
        source: vectorSource,
        type: 'LineString',
      });

      // Funkce pro odstrann vech interakc z mapy
      const clearInteractions = () => {
        map.getInteractions().forEach((interaction) => {
          if (interaction instanceof Draw) {
            map.removeInteraction(interaction);
          }
        });
      };

      // Odstranit star interakce ped pidnm nov
      clearInteractions();

      if (bollean) {
        // Pokud bollean je true, pidme logiku pro men dlky
        draw.on('drawend', (event) => {
          const feature = event.feature;
          const coordinates = feature.getGeometry().getCoordinates();

          // Transformace souadnic z EPSG:3857 na EPSG:4326
          const start = toLonLat(coordinates[0]);
          const end = toLonLat(coordinates[1]);

          // Vytvoen seky v Turf.js
          const line = turf.lineString([start, end]);

          // Vpoet dlky a azimutu
          const length = turf.length(line, { units: 'kilometers' }); // Dlka v kilometrech
          const bearing = turf.bearing(turf.point(start), turf.point(end)); // Azimut

          // Upozornn na vsledky
          console.log(miles)
          if (document.getElementById("km").style.backgroundColor === "crimson"){
            alert(`Length: ${length.toFixed(2)} km\nAzimuth: ${bearing.toFixed(2)}`);
          }else{
            alert(`Length: ${length.toFixed(2)/1.6} Miles\nAzimuth: ${bearing.toFixed(2)}`);
          }
         
        });
      } else {
        // Pokud bollean je false, pidme logiku pro men hlu
        draw.on('drawend', (event) => {
          const feature = event.feature;
          const coordinates = feature.getGeometry().getCoordinates();
          console.log(coordinates)
          console.log([lines, coordinates]);

          setLines((prevLines) => {
            const newLines = [...prevLines, coordinates];
            console.log(newLines);
            if (newLines.length === 2) {
              // Vypotat hel, pokud mme dv seky
              const angle = calculateAngle(newLines);
              if (document.getElementById("deg").style.backgroundColor === "crimson"){
                alert(`The angle is: ${angle.toFixed(2)}`);
              }else{
                var pi = Math.PI;
                alert(`The angle is: ${(angle.toFixed(2)*(pi/180))} Rad`);
              }
            
              return []; // Reset lines pro nov men
            }
            return newLines;
          });
         
        });
      }

      map.addInteraction(draw); // Pidn aktuln interakce na mapu
      setMap(map);
    }
  }, [bollean, map]);

  useEffect(() => {
    // Inicializace mapy pi prvnm renderovn
    const vectorSource = new VectorSource(); // Vektorov zdroj pro seky

    const vectorLayer = new VectorLayer({
      source: vectorSource,
    });

    // Vytvoen nov instance mapy OpenLayers
    const initialMap = new Map({
      layers: [
        new TileLayer({
          source: new OSM(), // Zdroj mapy - OpenStreetMap
        }),
        vectorLayer, // Pidn vektorov vrstvy do mapy
      ],
      view: new View({
        center: fromLonLat([0, 0]), // Poten pozice [dlka, ka] v EPSG:3857
        zoom: 2, // Poten rove piblen
      }),
      target: 'map', // ID HTML elementu, kam se mapa pipoj
    });

    setMap(initialMap); // Nastaven instance mapy do stavu

    return () => {
      initialMap.setTarget(null); // Odstrann mapy pi unmountovn
    };
  }, []); // useEffect spoutn pouze pi mountovn a unmountovn

  const calculateAngle = (lines) => {
    const commonPoint = lines[0][1]; // Pedpokldme, e seky maj spolen druh bod
    const firstLineStart = lines[0][0];
    const secondLineEnd = lines[1][1];

    // Transformace souadnic do EPSG:4326
    const point1 = toLonLat(firstLineStart);
    const point2 = toLonLat(commonPoint);
    const point3 = toLonLat(secondLineEnd);

    const bearingA = turf.bearing(turf.point(point2), turf.point(point1));
    const bearingB = turf.bearing(turf.point(point2), turf.point(point3));

    let angle = bearingB - bearingA;
    if (angle < 0) angle += 360; // Normalizace hlu
    return angle;
  };

  return (
    <div className="mapDiv">
      <div
        id="map" // ID HTML elementu, kter bude pouit jako cl pro vykreslen mapy
        style={{
          width: '100%',
          height: '500px', // Vka mapy
        }}
      ></div>
      <div className="buttons">

        <div className="lenghts">
        <button
          className="length"
          style={bollean ? { backgroundColor: 'crimson' } : { backgroundColor: 'aqua' }}
          onClick={() => setBollean(true)}
        >
          Length
        </button>
        <div className="">
        <button id='km' onClick={() => setMiles(false)}  style={!miles ? { backgroundColor: 'crimson' } : { backgroundColor: 'grey' }}>Km</button>
        <button id='mile' onClick={() => setMiles(true)}  style={miles ? { backgroundColor: 'crimson' } : { backgroundColor: 'grey' }}>Miles</button>
        </div>
        </div>
       
        <div className="angles">
        <button
          className="angle"
          style={!bollean ? { backgroundColor: 'crimson' } : { backgroundColor: 'aqua' }}
          onClick={() => setBollean(false)}
        >
          Angle
        </button>
         <div className="">
         <button id='deg' onClick={() => setRad(false)} style={!rad ? { backgroundColor: 'crimson' } : { backgroundColor: 'grey' }}>Degrees</button>
         <button onClick={() => setRad(true)} style={rad ? { backgroundColor: 'crimson' } : { backgroundColor: 'grey' }}>Rad</button>
         </div>
        </div>
       
      </div>
    </div>
  );
}

export default App;
